@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@500&display=swap);
.img {
    max-width: 100%;
    height: 180px;
    border-radius: 5px 5px 5px 5px;
    margin-bottom: 5px;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

.img:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

.grid {
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 40px;
    gap: 40px;
    padding: 20px;
}

.grid .details {
    background-color: rgb(20, 20, 31);
    padding: 20px;
    margin-top: -10px;
    border-radius: 2px 2px 5px 5px;
    box-shadow: 0 10px 10px rgb(0, 0, 0, 0.5);
}

.grid .details h3 {
    padding-bottom: 8px;
}

.grid .details h4 {
    padding-bottom: 4px;
}

.grid .details h4 span {
    margin-left: 8px;
    font-weight: 300;
}

@media (min-width: 640px) {
    .grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (min-width: 768px) {
    .grid {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (min-width: 992px) {
    .grid {
        padding: 20px 50px;
        grid-template-columns: repeat(4, 1fr);
    }
}
.header {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 10px 20px;
}

@media (min-width: 992px) {
    .header {
        padding: 20px 50px;
    }
}
.filter {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    padding: 20px;
}

.filter .form-control {
    width: 100%;
}

.filter .form-control input {
    background-color: rgb(26, 32, 41);
    border: none;
    outline: none;
    padding: 10px;
    color: rgb(216, 216, 216);
    border-radius: 5px;
    letter-spacing: 1px;
    width: 100%;
    margin: 0 0 30px;
}

.filter .form-control input::-webkit-input-placeholder {
    color: rgb(216, 216, 216);
}

.filter .form-control input:-ms-input-placeholder {
    color: rgb(216, 216, 216);
}

.filter .form-control input::placeholder {
    color: rgb(216, 216, 216);
}

.filter .region-filter {
    width: 100%;
}

.filter .region-filter .select {
    background-color: rgb(26, 32, 41);
    color: rgb(216, 216, 216);
    padding: 10px;
    border: none;
    border-radius: 5px;
    width: 200px;  
}

@media (min-width: 768px) {
    .filter {
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: space-between;
                justify-content: space-between;
    }

    .filter .region-filter {
        width: auto;
    }
    
    .filter .form-control {
        width: auto;
    }

    .filter .form-control input {
        width: auto;
        margin: 0;
    }
}

@media (min-width: 992px) {
    .filter {
        padding: 20px 50px;
    }
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Dosis', sans-serif;
    background-color: rgb(11, 15, 19);
    color: white;
}




